<template>
  <div class="index">
		<v-container>
			<div class="title" style="margin: 26px 0px 50px 0px; text-align: center;">
				<h1 class="display-3 blue--text font-weight-thin text-uppercase">Movies</h1>
				<h6 class="headline red--text font-weight-thin">Rights to the items listed below are reserved for their creators.</h6>
				<v-text-field v-model="searchMovie" label="Search..." style="width: 300px; margin: 50px auto 0px auto;" hint="Case sensitive"></v-text-field>
			</div>
			<div class="movies">
				<v-card v-for="(movie, index) in filteredMovie" :key="index">
					<v-img :src="movie.cover"></v-img>

					<v-card-title primary-title>
						<div>
							<h3 class="headline mb-0">{{ movie.title }}</h3>
							<h4 class="subheading grey--text">{{ movie.duration }}</h4>
						</div>
					</v-card-title>
					<v-divider></v-divider>
					<v-card-text>{{ movie.summary }}</v-card-text>

					<v-card-actions>
						<v-btn v-if="movie.available" flat color="accent" :href="movie.link" @click="logMovie(movie.title)">Watch</v-btn>
						<span v-if="!movie.available" class="red--text font-weight-medium" style="margin: 6px;">UNAVAILABLE</span>
					</v-card-actions>
				</v-card>
			</div>
		</v-container>
  </div>
</template>

<script>
import db from '@/firebase/init'
import firebase,{ messaging } from 'firebase'

export default {
  name: 'Movies',
  data() {
    return {
			movies: [],
			searchMovie: '',
			username: ''
    }
  },
  created() {
		this.username = this.$parent.$parent.$parent.username
		let ref = db.collection('movies').orderBy("title", "asc")

		ref.onSnapshot(snapshot => {
			snapshot.docChanges().forEach(change => {
				if(change.type === "added") {
					let doc = change.doc
					this.movies.push({
						id: doc.id,
						cover: doc.data().cover,
						duration: doc.data().duration,
						link: doc.data().link,
						summary: doc.data().summary,
						title: doc.data().title,
						available: doc.data().available
					})
				}
				if(change.type === "removed") {
					let doc = change.doc
					this.movies.splice(change.oldIndex, 1)
				}
				if(change.type === "modified") {
					let doc = change.doc
					this.movies.splice(change.oldIndex, 1, {
						id: doc.id,
						cover: doc.data().cover,
						duration: doc.data().duration,
						link: doc.data().link,
						summary: doc.data().summary,
						title: doc.data().title,
						available: doc.data().available
					})
				}
			})
		})
	},
	computed: {
		filteredMovie() {
			return this.movies.filter(movie => {
				return movie.title.match(this.searchMovie)
			})
		}
	},
	methods: {
		logMovie(movie) {
			this.$ga.event(this.username, 'is watching ' + movie)
		}
	}
}
</script>

<style scoped>
@media screen and (min-width: 1240px) {
	div.movies {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-gap: 16px;
		margin-bottom: 32px;
	}
}

@media screen and (max-width: 1240px) {
	div.movies {
		margin-bottom: 32px;
	}

	div.v-card__text {
		margin-bottom: 30px;
		margin-top: 30px;
		position: relative;
		bottom: 30px;
	}
}

h1 {
  text-align: center;
}

div.v-card {
	margin: 16px auto;
	max-width: 400px;
	width: 100%;
	height: 100%;
}

div.v-card__actions {
	position: absolute;
	bottom: 0px;
	margin-top: 16px;
}
</style>
